  background-repeat: no-repeat;
  background-position: center center; }

.hamburgerMenu__span__hamburgerMenu {
  position: relative; }

.hamburgerMenu__span {
  width: 10px;
  height: 2px;
  content: '';
  background: #1B1C21;
  display: inline;
  position: relative;
  display: block;
  left: 11px;
  top: 19px;
  background-color: #fff;
  border-radius: 1px; }

.hamburgerMenu__span::after {
  width: 14px;
  height: 2px;
  content: '';
  background: #1B1C21;
  display: inline;
  position: relative;
  display: block;
  top: 3px;
  background-color: #fff;
  border-radius: 1px; }

.hamburgerMenu__span::before {
  width: 18px;
  height: 2px;
  content: '';
  background: #1B1C21;
  display: inline;
  position: relative;
  display: block;
  top: -5px;
  background-color: #fff;
  border-radius: 1px; }

.mobile_menu_ShowHide {
  display: grid;
  width: 100%;
  position: fixed;
  grid-template-columns: 320px auto;
  top: 0;
  left: 0;
  z-index: 10;
  height: 100%; }

.SideMenuHeader {
  grid-column: 1/2; }

.MobileMenuBlur {
  grid-column: 2/3;
  flex-shrink: 1;
  background-color: #7E7E82;
  opacity: 0.1; }

.SideMenuWrapper--shadow {
  box-shadow: 39px -8px 44px -18px rgba(0, 0, 0, 0.75); }

.SideMenuWrapper--navFix {
  margin-bottom: 200px !important; }

.SideMenuWrapper--headerLabelFix {
  left: 271px !important;
  transform: translateX(0) !important; }

.SideMenuHeader__logo--positionFix {
  left: 72px !important; }

.SideMenu__mobile-button--close {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: #FF3E79;
  color: #EAEAEA;
  position: absolute;
  outline: none;
  border: none;
  top: 24px;
  left: 16px; }

.SideMenu__mobile-button--close:hover {
  transform: scale(1.2); }

.SideMenu_mobile-button-displayFix {
  display: none; }

@media (max-width: 565px) {
  .mobile-header__button--phone,
  .mobile-header__button--contact,
  .mobile-header__button--info {
    display: none; }
  .sectionAbout {
    flex-wrap: wrap; }
  .sectionAbout__logo {
    width: 100%;
    box-sizing: border-box;
    margin: 0;
    text-align: center;
    margin-top: 10px;
    order: -1;
    position: static !important; }
  .sectionAbout__text {
    width: 100%;
    box-sizing: border-box;
    max-height: 90px;
    padding-bottom: 5px;
    margin-bottom: 20px; }
  .sectionAbout-readMore {
    top: 90px; } }

.swiper-container {
  height: 100%;
  width: 100%; }

.swiper-slide {
  text-align: center;
  font-size: 18px;
  /* Center slide text vertically */
  display: -webkit-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center;
  align-items: center;
  justify-content: space-between; }

